import React, { useEffect, useMemo, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { auth, db } from '../firebase';
import { collection, addDoc, serverTimestamp, doc, getDoc } from 'firebase/firestore';
import { uploadMultipleFiles } from '../utils/storageUtils';
import Header from '../components/Header';
import './CategoryRequestForm.css';

const CATEGORY_CONFIGS = {
  transport: {
    label: 'Transport',
    icon: 'üöó',
    color: '#3b82f6',
    fields: [
      { name: 'pickup', label: 'Pickup Address', type: 'text', required: true, placeholder: 'Where should we pick you up?' },
      { name: 'drop', label: 'Drop Address', type: 'text', required: true, placeholder: 'Your destination address' },
      { name: 'rideTime', label: 'Preferred Time', type: 'datetime-local', required: false },
      { name: 'passengers', label: 'Passengers', type: 'number', min: 1, placeholder: '1' },
      { name: 'notes', label: 'Additional Notes', type: 'textarea', placeholder: 'Any special requirements?' }
    ]
  },
  repair: {
    label: 'Repair',
    icon: 'üîß',
    color: '#10b981',
    fields: [
      { name: 'appliance', label: 'What needs repair?', type: 'text', required: true, placeholder: 'Fan, AC, Fridge, Faucet...' },
      { name: 'issue', label: 'Describe the issue', type: 'textarea', required: true, placeholder: 'What seems to be the problem?' },
      { name: 'urgency', label: 'Urgency', type: 'select', required: true, options: ['Normal', 'Urgent', 'Emergency'] },
      { name: 'preferredTime', label: 'Preferred Time', type: 'datetime-local' }
    ]
  },
  education: {
    label: 'Education',
    icon: 'üìö',
    color: '#8b5cf6',
    fields: [
      { name: 'subject', label: 'Subject', type: 'text', required: true, placeholder: 'Maths, Science, English...' },
      { name: 'grade', label: 'Grade/Level', type: 'text', required: true, placeholder: 'e.g., Class 10 / Undergraduate' },
      { name: 'mode', label: 'Mode', type: 'select', required: true, options: ['Online', 'Offline'] },
      { name: 'sessionsPerWeek', label: 'Sessions per week', type: 'number', min: 1, placeholder: '3' }
    ]
  },
  home: {
    label: 'Home Help',
    icon: 'üè†',
    color: '#f59e0b',
    fields: [
      { name: 'serviceType', label: 'Service Type', type: 'select', required: true, options: ['Cleaning', 'Cooking', 'Baby Sitting', 'Elderly Care', 'Gardening'] },
      { name: 'hours', label: 'Hours Needed', type: 'number', min: 1, placeholder: '2' },
      { name: 'schedule', label: 'Schedule', type: 'select', options: ['One-time', 'Daily', 'Weekly', 'Monthly'] },
      { name: 'notes', label: 'Details', type: 'textarea', placeholder: 'Share any preferences or instructions' }
    ]
  },
  business: {
    label: 'Business',
    icon: 'üíº',
    color: '#6366f1',
    fields: [
      { name: 'requirement', label: 'Requirement', type: 'text', required: true, placeholder: 'Accounting, Data Entry, Marketing...' },
      { name: 'companyName', label: 'Company Name (Optional)', type: 'text' },
      { name: 'budget', label: 'Budget (Optional)', type: 'text', placeholder: '‚Çπ e.g., 5000' }
    ]
  },
  fitness: {
    label: 'Fitness',
    icon: 'üèãÔ∏è',
    color: '#ef4444',
    fields: [
      { name: 'goal', label: 'Fitness Goal', type: 'text', required: true, placeholder: 'Weight loss, Strength, Mobility...' },
      { name: 'location', label: 'Training Location', type: 'select', options: ['Home', 'Gym', 'Outdoor'] },
      { name: 'sessionLength', label: 'Session Length (mins)', type: 'number', min: 15, placeholder: '60' }
    ]
  },
  shopping: {
    label: 'Shopping',
    icon: 'üõí',
    color: '#14b8a6',
    fields: [
      { name: 'items', label: 'Items List', type: 'textarea', required: true, placeholder: 'List items you need with quantities' },
      { name: 'store', label: 'Preferred Store (Optional)', type: 'text' },
      { name: 'deliverBy', label: 'Deliver By', type: 'datetime-local' }
    ]
  },
  more: {
    label: 'Other',
    icon: '‚ûï',
    color: '#64748b',
    fields: [
      { name: 'title', label: 'Service Title', type: 'text', required: true, placeholder: 'What do you need help with?' },
      { name: 'details', label: 'Details', type: 'textarea', required: true, placeholder: 'Describe your requirement' }
    ]
  }
};

const fieldInitialValue = (field) => {
  switch (field.type) {
    case 'text':
    case 'textarea':
    case 'select':
      return '';
    case 'number':
      return '';
    case 'datetime-local':
      return '';
    default:
      return '';
  }
};

const CategoryRequestForm = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const [userName, setUserName] = useState('');
  const [userPhone, setUserPhone] = useState('');
  const [attachments, setAttachments] = useState([]);
  const [submitting, setSubmitting] = useState(false);
  const [errors, setErrors] = useState({});

  const config = useMemo(() => CATEGORY_CONFIGS[id] || CATEGORY_CONFIGS.more, [id]);

  const [formData, setFormData] = useState(() => {
    const obj = {};
    (config.fields || []).forEach((f) => { obj[f.name] = fieldInitialValue(f); });
    obj.preferredDate = '';
    obj.locationNote = '';
    obj.budget = '';
    return obj;
  });

  useEffect(() => {
    const unsub = auth.onAuthStateChanged(async (user) => {
      if (!user) return;
      try {
        const snap = await getDoc(doc(db, 'users', user.uid));
        const data = snap.exists() ? snap.data() : {};
        setUserName(data.fullName || user.displayName || '');
        setUserPhone(data.mobileNumber || (user.phoneNumber || '').replace('+91', '') || '');
      } catch (_) {}
    });
    return () => unsub();
  }, []);

  const handleChange = (name, value) => {
    setFormData((prev) => ({ ...prev, [name]: value }));
    if (errors[name]) setErrors((e) => ({ ...e, [name]: '' }));
  };

  const validate = () => {
    const newErrors = {};
    (config.fields || []).forEach((f) => {
      if (f.required && !String(formData[f.name] || '').trim()) newErrors[f.name] = 'Required';
    });
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const onFilePick = (e) => {
    const files = Array.from(e.target.files || []);
    if (files.length) setAttachments((prev) => [...prev, ...files]);
  };

  const removeAttachment = (index) => {
    setAttachments((prev) => prev.filter((_, i) => i !== index));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!validate()) return;
    setSubmitting(true);
    try {
      const user = auth.currentUser;
      const locationRaw = localStorage.getItem('userLocation');
      let location = null;
      try { location = locationRaw ? JSON.parse(locationRaw) : null; } catch (_) {}

      // Upload attachments if any
      let uploaded = { successful: [], failed: [] };
      if (attachments.length > 0) {
        uploaded = await uploadMultipleFiles(attachments, `service-attachments/${id}`, user?.uid || userPhone || 'anon');
      }

      const payload = {
        categoryId: id,
        categoryLabel: config.label,
        icon: config.icon,
        color: config.color,
        form: formData,
        attachments: uploaded.successful,
        attachmentErrors: uploaded.failed,
        user: {
          uid: user?.uid || null,
          name: userName,
          mobileNumber: userPhone,
        },
        location: location ? {
          address: location.address || location.name || '',
          lat: location.lat || null,
          lng: location.lng || null,
        } : null,
        status: 'new',
        createdAt: serverTimestamp(),
      };

      await addDoc(collection(db, 'serviceRequests'), payload);

      alert('‚úÖ Request submitted! We will notify nearby buddies.');
      navigate('/home');
    } catch (err) {
      console.error(err);
      alert('Failed to submit. Please try again.');
    } finally {
      setSubmitting(false);
    }
  };

  return (
    <div className="category-form-page">
      <Header userName={userName} />

      <main className="category-form-content">
        <form className="category-card" onSubmit={handleSubmit}>
          <div className="category-header" style={{ ['--cat-color']: config.color }}>
            <div className="cat-icon" aria-hidden>
              {config.icon}
            </div>
            <div className="cat-titles">
              <h2 className="cat-title">{config.label}</h2>
              <p className="cat-subtitle">Tell us what you need. We will route this to the best buddy.</p>
            </div>
          </div>

          <div className="fields-grid">
            {(config.fields || []).map((f) => (
              <div key={f.name} className="form-group">
                <label className="label" htmlFor={f.name}>{f.label}{f.required ? ' *' : ''}</label>
                {f.type === 'textarea' ? (
                  <textarea
                    id={f.name}
                    className={`input textarea ${errors[f.name] ? 'error' : ''}`}
                    placeholder={f.placeholder || ''}
                    value={formData[f.name]}
                    onChange={(e) => handleChange(f.name, e.target.value)}
                    rows={4}
                  />
                ) : f.type === 'select' ? (
                  <select
                    id={f.name}
                    className={`input select ${errors[f.name] ? 'error' : ''}`}
                    value={formData[f.name]}
                    onChange={(e) => handleChange(f.name, e.target.value)}
                  >
                    <option value="">Select</option>
                    {(f.options || []).map((opt) => (
                      <option key={opt} value={opt}>{opt}</option>
                    ))}
                  </select>
                ) : (
                  <input
                    id={f.name}
                    type={f.type}
                    className={`input ${errors[f.name] ? 'error' : ''}`}
                    placeholder={f.placeholder || ''}
                    value={formData[f.name]}
                    min={f.min}
                    onChange={(e) => handleChange(f.name, e.target.value)}
                  />
                )}
                {errors[f.name] && <span className="hint error-text">{errors[f.name]}</span>}
              </div>
            ))}

            {/* Common optional fields */}
            <div className="form-group">
              <label className="label" htmlFor="preferredDate">Preferred Date</label>
              <input
                id="preferredDate"
                type="date"
                className="input"
                value={formData.preferredDate}
                onChange={(e) => handleChange('preferredDate', e.target.value)}
              />
            </div>

            <div className="form-group">
              <label className="label" htmlFor="budget">Budget (Optional)</label>
              <input
                id="budget"
                type="text"
                className="input"
                placeholder="‚Çπ Approx budget"
                value={formData.budget}
                onChange={(e) => handleChange('budget', e.target.value)}
              />
            </div>

            <div className="form-group full">
              <label className="label">Attachments (Optional)</label>
              <div className="upload-area">
                <input type="file" accept="image/*" multiple onChange={onFilePick} />
                <span className="upload-hint">Add photos to explain your request better</span>
              </div>
              {attachments.length > 0 && (
                <div className="attachments">
                  {attachments.map((f, i) => (
                    <div key={i} className="chip">
                      <span>{f.name}</span>
                      <button type="button" onClick={() => removeAttachment(i)} aria-label="Remove">√ó</button>
                    </div>
                  ))}
                </div>
              )}
            </div>
          </div>

          <div className="actions">
            <button type="button" className="btn secondary" onClick={() => navigate('/home')}>Cancel</button>
            <button type="submit" className="btn primary" disabled={submitting}>
              {submitting ? 'Submitting...' : 'Submit Request'}
            </button>
          </div>
        </form>
      </main>
    </div>
  );
};

export default CategoryRequestForm;


